.cursor {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  border: 2px solid white;
  position: fixed;
  top: 0;
  left: 0;
  z-index:400;
  pointer-events: none;
}

/* Scrollbar */
*::-webkit-scrollbar {
  width: 6px;
}

*::-webkit-scrollbar-track {
  background: black;
}

*::-webkit-scrollbar-thumb {
  background-color: white;
  /* border-radius: 10px; */
  border: 1px solid black;
}

.progress {
  position: fixed;
  left: 0;
  right: 0;
  height: 4px;
  background: white;
  top: 0px;
  z-index: 5;
}
